<template>
  <dropdown-menu>
    <span slot="dropdown-title">
      <span
        v-if="query.price_min && query.price_max"
      >{{ convertNumToCurrency(query.price_min, 'short') }} - {{ convertNumToCurrency(query.price_max, 'short') }}</span>
      <span
        v-else-if="query.price_min"
      >{{ convertNumToCurrency(query.price_min, 'short') }} - Unlimited</span>
      <span v-else-if="query.price_max">$ 0 - {{ convertNumToCurrency(query.price_max, 'short') }}</span>
      <span v-else>Price</span>
    </span>
    <div slot="dropdown-fields">
      <div class="price block sm:flex">
        <div class="min-price flex">
          <label for="min-price" class="flex items-center w-24">Minimum:</label>
          <select
            v-if="query.price_max"
            v-model="price_min"
            name="min-price"
            id="min-price"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
          >
            <option
              v-for="(price, index) in priceMinSlice"
              v-bind:value="price"
              v-bind:key="index"
            >{{ convertNumToCurrency(price, 'long') }}</option>
          </select>
          <select
            v-else
            v-model="price_min"
            name="min-price"
            id="min-price"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
          >
            <option
              v-for="(price, index) in optionPrice"
              v-bind:value="price"
              v-bind:key="index"
            >{{ convertNumToCurrency(price, 'long') }}</option>
          </select>
        </div>

        <div class="max-price flex mt-3 sm:mt-0 sm:ml-6">
          <label for="max-price" class="flex items-center w-24">Maximum:</label>
          <select
            v-if="query.price_min"
            v-model="price_max"
            name="max-price"
            id="max-price"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
          >
            <option
              v-for="(price, index) in priceMaxSlice"
              v-bind:value="price"
              v-bind:key="index"
            >{{ convertNumToCurrency(price, 'long') }}</option>
          </select>
          <select
            v-else
            v-model="price_max"
            name="max-price"
            id="max-price"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
          >
            <option
              v-for="(price, index) in optionPrice"
              v-bind:value="price"
              v-bind:key="index"
            >{{ convertNumToCurrency(price, 'long') }}</option>
          </select>
        </div>
      </div>
    </div>
  </dropdown-menu>
</template>

<script>
import numeral from "numeral";
import DropdownMenu from "../DropdownMenu";
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "SearchDropdownPrice",
  components: {
    "dropdown-menu": DropdownMenu
  },
  data() {
    return {
      optionPrice: [
        0,
        100000,
        200000,
        300000,
        400000,
        500000,
        600000,
        700000,
        800000,
        900000,
        1000000,
        1250000,
        1500000,
        1750000,
        2000000,
        5000000,
        10000000,
        20000000,
        50000000
      ]
    };
  },
  computed: {
    ...mapGetters(["query"]),
    price_min: {
      get() {
        return this.query.price_min;
      },
      set(price) {
        this.selectMinPrice(price);
      }
    },
    price_max: {
      get() {
        return this.query.price_max;
      },
      set(price) {
        this.selectMaxPrice(price);
      }
    },
    priceMinSlice() {
      return this.optionPrice.slice(
        0,
        this.optionPrice.indexOf(this.query.price_max)
      );
    },
    priceMaxSlice() {
      return this.optionPrice.slice(
        this.optionPrice.indexOf(this.query.price_min) + 1,
        this.optionPrice.length
      );
    }
  },
  methods: {
    ...mapMutations(["selectMinPrice", "selectMaxPrice"]),
    convertNumToCurrency(value, length) {
      if (length === "long") {
        return numeral(value).format("$ 0,0[.]00");
      } else if (length === "short") {
        return numeral(value).format("$ 0.00 a");
      }
    }
  }
};
</script>