<template>
  <search-dropdown class="mt-3 sm:mt-0 sm:ml-2">
    <span slot="dropdown-title">
      <span class="font-semibold" v-if="query.beds_min"
        >{{ query.beds_min }}+</span
      >
      Beds &
      <span class="font-semibold" v-if="query.baths_min"
        >{{ query.baths_min }}+</span
      >
      Baths
    </span>
    <div slot="dropdown-fields">
      <div class="beds-baths block xs:flex xs:items-center">
        <div class="beds flex items-center xs:flex-1">
          <label for="beds" class="w-24">Bedroom:</label>
          <select
            name="beds"
            id="beds"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
            v-model="beds_min"
          >
            <option
              v-for="(n, index) in 6"
              v-bind:value="n - 1"
              v-bind:key="index"
            >
              {{ n - 1 }}+
            </option>
          </select>
        </div>
        <div class="baths flex items-center xs:flex-1 mt-3 xs:mt-0 xs:ml-6">
          <label for="baths" class="w-24">Bathroom:</label>
          <select
            name="baths"
            id="baths"
            class="appearance-none focus:outline-none bg-gray-200 border border-teal-500 py-1 px-3 rounded-none flex-1"
            v-model="baths_min"
          >
            <option
              v-for="(n, index) in 6"
              v-bind:value="n - 1"
              v-bind:key="index"
            >
              {{ n - 1 }}+
            </option>
          </select>
        </div>
      </div>
    </div>
  </search-dropdown>
</template>

<script>
import DropdownMenu from "../DropdownMenu";
import { mapGetters } from "vuex";
import { mapMutations } from "vuex";

export default {
  name: "SearchDropdownBedBath",
  components: {
    "search-dropdown": DropdownMenu,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["query"]),
    beds_min: {
      get() {
        return this.query.beds_min;
      },
      set(bedsNum) {
        this.selectBeds(bedsNum);
      },
    },
    baths_min: {
      get() {
        return this.query.baths_min;
      },
      set(bathsNum) {
        this.selectBaths(bathsNum);
      },
    },
  },
  methods: {
    ...mapMutations(["selectBeds", "selectBaths"]),
  },
};
</script>

