<template>
  <div id="search-location" class="relative w-full lg:w-1/3 shadow-md z-10">
    <div class="search-form">
      <form
        v-on:keyup.prevent="autocompleteLocationHouse"
        v-on:enter.prevent="autocompleteLocationFirstHouse"
        class="search flex items-center bg-teal-500"
      >
        <input
          type="text"
          name="search-location"
          placeholder="Search city or postal code..."
          class="bg-gray-200 border border-teal-500 focus:outline-none focus:bg-gray-100 w-full pl-2 py-1 px-3 rounded-none"
          v-model="searchLocation"
        />
      </form>

      <div v-if="autocompleteResults" class="absolute shadow-lg bg-gray-200 my-2 w-full">
        <ul
          v-for="(result, index) in autocompleteResults"
          v-bind:key="index"
          class="autocomplete-result divide-y"
        >
          <li
            v-if="result.area_type === 'postal_code'"
            v-on:click="selectLocationHouse(result)"
            class="px-2 py-1 hover:bg-teal-500 hover:text-gray-100 cursor-pointer"
          >{{ result.postal_code }}, {{ result.state_code }}</li>
          <li
            v-if="result.area_type === 'city'"
            v-on:click="selectLocationHouse(result)"
            class="px-2 py-1 hover:bg-teal-500 hover:text-gray-100 cursor-pointer"
          >{{ result.city }}, {{ result.state_code }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "SearchLocationText",
  data() {
    return {
      searchLocation: ""
    };
  },
  computed: {
    ...mapGetters(["autocompleteResults"])
  },
  methods: {
    ...mapMutations(["selectLocation", "autocompleteReset"]),
    ...mapActions(["autocompleteLocation"]),
    selectLocationHouse(location) {
      if (location.area_type === "city") {
        this.searchLocation =
          `${location.city}, ${location.state_code}` ||
          `${location.postal_code}, ${location.state_code}`;
      } else if (location.area_type === "postal_code") {
        this.searchLocation = location.postal_code;
      }
      this.selectLocation(location);
      this.autocompleteReset();
    },
    async autocompleteLocationHouse() {
      await this.autocompleteLocation(this.searchLocation);
    },
    autocompleteLocationFirstHouse() {}
  }
};
</script>