<template>
  <router-link
    v-if="property.listing_id"
    :to="{ name: 'PropertyInfo', params: { property_id: property.property_id }}"
    class="max-w-sm rounded overflow-hidden shadow-md m-2 w-84 transition duration-300 ease-in-out transform hover:scale-105 hover:shadow-lg"
  >
    <div class="image md:w-84 h-48 overflow-hidden">
      <img
        class="object-cover object-center w-full h-full"
        v-bind:src="property.thumbnail"
        alt="property picture"
      />
    </div>

    <div class="p-3">
      <p class="text-teal-500 text-sm">
        <span v-if="property.beds">{{ property.beds }} beds</span>
        <span class="mx-2">|</span>
        <span v-if="property.baths">{{ property.baths || 0 }} baths</span>
        <span class="mx-2">|</span>
        <span
          v-if="property.building_size"
        >{{ property.building_size.size }} {{ property.building_size.units }}</span>
      </p>
      <h3 class="text-sm mb-2 pt-3">
        {{ property.address.line }}
        {{ property.address.city }}, {{ property.address.state_code }} {{ property.address.postal_code }}
      </h3>
      <p class="text-lg font-bold">{{ convertNumToCurrency(property.price) }}</p>
    </div>
  </router-link>
</template>

<script>
import numeral from "numeral";

export default {
  props: ["property"],
  data() {
    return {};
  },
  methods: {
    convertNumToCurrency(value) {
      return numeral(value).format("$ 0,0[.]00");
    }
  }
};
</script>